/*
  A stylesheet customizing app (custom renderers)
*/

table.htCore td.star {
    color: #fcb515;
  }
  
  table.htCore tr.odd td {
    background: #fafbff;
  }
  
  table.htCore td .progressBar {
    background: #37bc6c;
    height: 10px;
  }
  
  table.htCore tr.selected td {
    background: #edf3fd;
  }
  
  /*
    A stylesheet customizing Handsontable style
  */
  
  .collapsibleIndicator {
    text-align: center;
  }
  
  .handsontable .htRight .changeType {
    margin: 3px 1px 0 13px;
  }
  .handsontable .green {
    background: #37bc6c;
    font-weight: bold;
  }
  
  .handsontable .orange {
    background: #fcb515;
    font-weight: bold;
  }
  
  .btn {
    padding: 20px;
    font: 1.4em sans-serif;
  }
  [data-color="green"] {
    background: #37bc6c;
  }
  [data-color="orange"] {
    background: #fcb515;
  }
  
.buttons {
  margin-left: 10px;
}

.buttons button {
  margin-top: 8px;
}

.dropzone {
  display: inline-flex;
  width: auto;
  padding: 20px;
  margin: 0 auto;
}

.htInvalid {
  background-color: red;
}